import React from "react";
import { useState, useEffect } from "react";

const BtcPriceCoinPaprika = () => {
  const [price, setPrice] = useState(0);
  useEffect(() => {
    getPrice();
  }, []);

  const getPrice = async () => {
    const url = "https://api.coinpaprika.com/v1/tickers/btc-bitcoin";
    const response = await fetch(url);
    const data = await response.json();
    const price = Number(data.quotes.USD.price);
    setPrice(price.toFixed(0));
  };

  return (
    <>
      <div className="product">
        <div>
          <img
            src="data:image/png;base64,"
            alt=""
          />
        </div>
        <p>Price from CoinPaprika</p>
        <h3>{price} USD / BTC</h3>
      </div>
    </>
  );
};

export default BtcPriceCoinPaprika;
